.RoomCodeText{
  color:#d2003c ;
  text-align: center;
  font-size: 4rem;
}

.creator{
  color: #ffcc00;
  text-align: center;
  font-size: 1.5rem;
  text-transform:capitalize;
}

.joinedPlayers{
  display:flex;
  border-radius: 8px;
  background: #ffcc00;
  color: #2e2a2a;
  min-width:275px;
  padding: 0px;
  text-transform:capitalize;
  margin: 7px !important;
  text-align: left;
}

.player{
  flex: 0.9;
  margin-left: 10px;
}

.close{
  cursor: pointer;
  padding: 2rem;
  color: #2e2a2a !important;
}

.close:hover{
  background:rgba(0, 48, 95, 0.219)
}

#gameCodeHead {
  font-size: 12px;
  text-align: center;
  color: #d2003c;
  text-transform: uppercase;
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(210, 0, 60, 0),
    rgba(210, 0, 60, 0.75),
    rgba(210, 0, 60, 0)
  );
  margin: 1rem 0;
}

#startGame {
  background: #d2003c;
  margin: 1rem 0;
  padding: 0rem 0;
  width:100%;
  color:#ffffff;
  text-decoration:none;
  text-align:center;
  border-radius:10px;
}

#startGame:hover{
  background: #c4133f;  
}

#gameCodeHead {
  font-size: 12px;
  text-align: center;
  color: #d2003c;
  text-transform: uppercase;
}

#creatorLobby {
  width: 100%;
  max-height: 275px;
  overflow-y: auto;
}

@media only screen and (max-height:700px) {
  div#creatorLobby{
    max-height: 200px;
    overflow-y: scroll;
    position: relative;
  }
}

@media only screen and (max-height:600px) {
  div#creatorLobby{
    max-height: 120px;
    overflow-y: scroll;
  }
  .RoomCodeText {
    font-size: 3rem;
  }
}


