  flex-direction: column;
  position: sticky;
  top: 0px;
  z-index: 1100;
  background: #0f0069;
  /* box-shadow: 0 5px 15px #0f0069; */
}

.navbar .cross-icon{
  font-size: 1rem;
}

.nav__content{
