  position: absolute; */
  background: rgb(0, 30, 60);

  max-width: 1350px;
    margin: 0 auto;
    /* font-family: var(--font-family--text-default,''),var(--font-family--text-fallback); */
    font-weight: 700;
    font-size: 14px;
    line-height: 1;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    /* padding: 30px 25px 50px 25px; */
    padding-top: 30px;
    z-index: 1600;
    position: absolute;
    width:100%;
}

.nav__content a{
  color: #fff;
  text-decoration: none;
  text-align: center;
  font-family: Helvetica,sans-serif;
  /* font-weight: 700;
  font-family: var(--font-family--text-default,''),var(--font-family--text-fallback); */
  line-height: 1;
  padding-top: 20px;
  font-size: 14px;
  text-transform: uppercase;
}

.nav__content :last-child{
  margin-bottom: 100rem;
}

.header{
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  justify-content: center;
  /* margin-right: 2rem; */
  max-height: 110px;
}

.header__left{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.menu-header__logo{
  width: 100px;
  z-index: 12;
  margin: 5px;
}

.header__menuIcon{
  margin-left: 0.3rem;
  background-color: #0f0069;
  border-radius: 20px;
  border: 1px solid rgba(255,255,255,.2);
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  cursor: pointer;
}

.header__menuIcon:hover{
  background-color: #0f0069;
}

.header__menuIcon i{
  color: #fff;
  margin: 0.5px;
  font-size: 12px;
}

.header__menuIcon svg{
  fill: #fff;
}

