.main-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;   
    min-height: 50vh;
    color: whitesmoke;
  
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 2rem;
    position: relative;
    text-align: center;
    border-radius: 10px !important;
}

.create-game-container{
    background-color: rgba(0, 48, 95, 0.219);
    border-radius: 10px !important;
}

.input-box{
    padding: 0.8rem;
    width: 93%;
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 1rem !important;    
}

button{
    width: 100%;
}

.back-create-game{
    background: #d2003c !important ;
}

.join-create-game{
    cursor: pointer !important;
}

.back-create-game:hover{
    background: #e4073e !important;
}






 