.main-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  min-height: 50vh;
  padding: 1rem;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem;
  position: relative;
}

#redbullCan {
  width: 4rem;
  /* animation: shake 3s !important; */
  /* animation-iteration-count: infinite !important; */
}

#warn {
  color: #645b5e;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  margin-top: 1rem;
}

#create {
  box-shadow: 0 5px 15px rgb(145 92 182 / 40%);
  background: #d2003c;
  width: 100%;
  text-align: center;
  color: whitesmoke;
  text-decoration: none;
  border-radius: 10px;
  margin: 1rem;
  cursor: pointer;
  font-size: 20px;
}

#or-container {
  color: whitesmoke;
  width: 100%;
  position: relative;
}

#or-container span {
  position: absolute;
  top: 3px;
  left: 48%;
  background-color: rgba(0, 15, 30, 0.973);
  padding: 3px;
  border-radius: 15px;
}

#or-container hr {
  border: 0;
  height: 2px;
  background-image: linear-gradient(
    to right,
    rgba(210, 0, 60, 0),
    rgba(210, 0, 60, 0.75),
    rgba(210, 0, 60, 0)
  );
  width: 100%;
}

#create:hover {
  background: #d2003cd2;
}

#join {
  box-shadow: 0 5px 15px rgba(254, 202, 10, 0.4);
  text-align: center;
  width: 100%;
  background: #ffcc00;
  color: #2e2a2a;
  border-radius: 10px;
  text-decoration: none;
  margin-top: 1rem;
  cursor: pointer;
  border: none;
}

#join:hover {
  background: #ffcc00d2;
}

#instruction {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 2rem;
}

#instruction p {
  color: whitesmoke;
  font-size: 24px;
  margin-left: 30px;
  text-align: left;
}

#instruction span {
  color: #f6c702;
  /* animation: glow 3s !important; */
  /* animation-iteration-count: infinite !important; */
}

@media only screen and (max-width: 400px) {
  #logo-img {
    width: 10rem;
  }

  #redbullCan {
    width: 4rem;
  }

  #instruction p {
    font-size: 20px;
  }
}

@media only screen and (max-height: 570px) {
  #logo-img {
    width: 10rem;
  }

  #redbullCan {
    width: 2rem;
  }

  #instruction p {
    font-size: 14px;
  }
}

@media only screen and (max-height: 470px) {
  
  #instruction {
    margin-bottom: 0rem;
  }

  p{
    margin: 5px;
  }
 
  #join {
    margin: 0px;
  }

  #create {
    margin: 0px;
    margin-top: 5px;
  }
  #redbullCan {
    width: 1.5rem;
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(-8deg);
  }
  50% {
    transform: translate(1px, 1px) rotate(8deg);
  }
  75% {
    transform: translate(1px, 1px) rotate(-8deg);
  }
  100% {
    transform: translate(1px, 1px) rotate(8deg);
  }
}

@keyframes glow {
  0% {
    color: #fdd32a;
  }

  100% {
    color: #d2003c;
  }
}


