#continue {
  box-shadow: 0 5px 15px rgb(145 92 182 / 40%);
  font-size: 24px;
  padding: 10px 0;
  background: #d2003c !important;
  border: none;
  width: 85%;
  margin: 0 auto;
}

#get-redbull {
  display: block;
  text-decoration: none;
  box-shadow: 0 5px 15px rgb(251 213 14 / 40%);
  font-size: 24px;
  color: #2c2f35;
  padding: 10px 0;
  background: #fbd50e !important;
  width: 100%;
  border-radius: 7px;
  margin: 0 auto;
  margin-top: 1rem;
}

