@media only screen and (min-width: 1100px) {
  #card {
    width: 25%;
  }
}

.WinnerWrapper{
  background: #0f0069;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-top: 2rem;
  padding-bottom: 2rem;
  width: 100%;
}

.WinnerInner{
  position: relative;
  z-index: 1;
  width: 100%;
}

.Winner{
  text-align: center;
  color: #ffcc00 !important;
  font-size: 2rem;
  margin: -10px;
  text-transform: uppercase !important; 
}

.WinnerP{  
  color: #d1d2d4 !important;
  text-align: center !important;
  width: 80% !important;
  margin: 0 auto !important;
  margin-top: 1rem !important;
}

#seeagain{
  box-shadow: 0 5px 15px rgb(251 213 14 / 40%);
  padding: 10px 0;
  background: #d2003c !important;
  width: 80%;
  border-radius: 7px;
  margin: 0 auto;
  margin-top:1rem;
  font-size: 24px !important;
  text-align: center;
  color: whitesmoke;
}

#imgbig{
  width: auto;
  height: 260px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

@keyframes glow {
  0% {
    color: #fdd32a;
  }
  100% {
    color: #d2003c;
  }
}

@media only screen and (min-height: 670px) {
  body{
    height: 100%;
    overflow: hidden !important;
  }
  a#get-redbull {
    width: 80%;
  }
  div#seeagain {
    width: 80%;
  }
}

@media only screen and (max-height: 670px) {
  body{
    height: 100%;
    overflow: hidden !important;
  }
  a#get-redbull {
    width: 80%;
    margin-top: 10px;
  }
  div#seeagain {
    width: 80%;
  }
  div.Looser {
    font-size: 1.5rem !important;
  }
}

@media only screen and (max-height: 568px) {
  a#get-redbull {
    font-size: 16px !important;
  }
  div#seeagain {
    font-size: 16px !important;
  }
  p.Winner {
    margin: auto;
    font-size: 1.5rem !important;
  }
  div.WinnerWrapper {
    padding-top: 0rem;
  }
}