.button{
    cursor: pointer;
}

#continue {
  box-shadow: 0 5px 15px rgb(145 92 182 / 40%);
  font-size: 24px;
  padding: 10px 0;
  background: #d2003c !important;
  border: none;
  width: 85%;
  margin: 0 auto;
}

.get-redbull{
  color:  #fbd50e !important;
  text-decoration: none !important;
  animation: glow 3s !important;
  animation-iteration-count: infinite !important;
}

#finish div {
  font-size: 16px;
  text-align: center;
  color: #000 !important;
}

@media only screen and (min-width: 1100px) {
  #card {
    width: 25%;
  }
}

.LooserWrapper{
  background: #0f0069;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-top: 2rem;
  padding-bottom: 2rem;
  
}
  
.LooserInner{
  position: relative;
  z-index: 1;
}

.Looser{
  text-align: center;
  color: #ffcc00 !important;
  font-size: 26px !important;
  text-transform: uppercase !important;
}

.WinnerP{  
  color: #d1d2d4 !important;
  text-align: center !important;
  width: 80% !important;
  margin: 0 auto !important;
  margin-top: 1rem !important;
}

@keyframes glow {
  0% {
    color: #fdd32a;
  }
  100% {
    color: #d2003c;
  }
}
