.main-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    min-height: 50vh;
    color: whitesmoke;
}

.container{
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 0rem;
    position: relative;
    text-align: center;
    background-color: #0f0069;
    border-radius: 10px !important;
}

h2 {
    color: #ffcc00;
}

.create-game-container{
    background-color: #0f0069;
    border-radius: 10px !important;
}

.input-box{
    padding: 0.8rem;
    width: 97%;
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 1rem !important;
}

.back-create-game{
    background: #d2003c !important ;
    width: 100%;
    border-radius: 10px;
    text-decoration: none;
    color: whitesmoke;
}

.back-create-game:hover{
    background: #d40f40 !important;
   
}

.join-creategame{
    background: #ffcc00;
}

.join-creategame:hover{
    background: #ffcc00d2;
}

@media only screen and (max-width: 430px) {
    .input-box{
        width: 93%;
        padding: 14px;
    }
    h2{padding: 0px;}
    /* div#blank {
        height: 20px;
    } */
}

@media only screen and (max-width: 320px) {
    .input-box{
        width: 93%;
        padding: 10px;
    }
    h2{padding: 0px;}
    /* div#blank {
        height: 0px;
    } */
}
