@font-face {
  font-family: 'BullText-Medium';
  src: local('BullText-Medium'), url('./fonts/BullText-Medium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'BullText-Bold';
  src: local('BullText-Bold'), url('./fonts/BullText-Bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.font-face-bm {
  font-family: "BullText-Medium";
}

.font-face-bb {
  font-family: "BullText-Bold";
}

p, h2{
  font-family: "BullText-Medium";
}

body {
  margin: 0;
  padding: 0;
  background-color: #0f0069;
  overflow: hidden;
}

ul {
  margin: 0;
}

/* p{
  margin: 15px;
} */

.noselect {
  -webkit-user-select: none;
}

.no-scroll-display::-webkit-scrollbar {
  width: 0.5rem;
  height: 0px;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.CookieConsent {
  display: none !important;
  color: black !important;
  background-color: white !important;
  box-shadow: 0px 2px 2px 2px grey;
}

#rcc-decline-button > div {
  display: flex !important;
}

#rcc-decline-button {
  background: none !important;
  color: #d2003c !important;
  text-decoration: underline !important;
}

#rcc-confirm-button {
  margin: 0px !important;
  color: white !important;
  background-color: #d2003c !important;
  border-radius: 9px !important;
}

.grid-container {
  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 9rem 2fr 45vh;
  height: 100%;
}

header {
  grid-area: header;
  background-color: rgb(252, 252, 252);
  box-shadow: 0px 0px 10px 0px #f0c040;
  margin-bottom: 10px;
  max-width: 100vw;
}

main {
  margin-bottom: 5rem;
  background-color: red;
}

footer {
  grid-area: footer;
  background-color: rgb(219, 219, 219);
  color: black;
  max-width: 100vw;
}

@media only screen and (max-width:430px) {
  body{
    height: 100%;
    overflow: hidden !important;
  }
}