.Finalscore{
  color: whitesmoke;
  font-size: 26px;
}

#continue {
  box-shadow: 0 5px 15px rgb(145 92 182 / 40%);
  font-size: 26px;
  padding: 1rem 0;
  background: #d2003c;
  border: none;
  width: 85%;
  margin: 0 auto;
}

.PlayerScoresWrapper{  
  padding: 10px;
  width: 80%;
  margin: 0 auto;
  max-height: 650px;
  overflow-y: scroll;
}

.PlayerInfo{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.PlayerName{
  color: #fff;
  font-size: 24px;
  font-family: 'BullText-Bold';
}

.span-scoreStats {
  color: #fff;
  font-family: 'BullText-Bold';
  font-size: 24px;
}

.Points{
  color: #fff !important;
  font-size: 14px !important;   
}

#seeWinnerBtn {
  box-shadow: 0 5px 15px rgb(251 213 14 / 40%);
  font-size: 24px;
  color: #2c2f35;
  padding: 10px 0;
  background: #fbd50e !important;
  width: 80%;
  border-radius: 7px;
  margin: 0 auto;
  margin-top: 2rem;
}

@media only screen and (max-height: 570px) {
  div.PlayerScoresWrapper {
    max-height: 300px !important;
    margin-top: -15px !important;
  }
}

@media only screen and (max-height: 750px) {
  p.Finalscore {
    font-size: 24px;
    margin-top: 0px;
  }
  div.PlayerScoresWrapper {
    max-height: 380px;
    margin-top: 10px;
  }
  div.PlayerName {
    font-size: 20px;
    font-family: 'BullText-Bold';
  }
  span.span-scoreStats {
    font-size: 20px;
    font-family: 'BullText-Bold';
  }
}

@media only screen and (min-height: 750px) {
  p.Finalscore {
    margin-top: 10px;
  }
  div.PlayerScoresWrapper {
    /* max-height: 400px; */
    margin-top: 10px;
  }
  div#seeWinnerBtn {
    margin-top: 1.5rem;
  }
}